
import { Vue, Options } from "vue-class-component"
import Placeholder from "@/components/Placeholder.vue"
import data from "@/data/data.json"
import SideNews from "@/components/SideNews.vue"
import Social from "@/components/Social.vue"
import Footer from "@/components/Footer.vue"
import { Ref } from "vue-property-decorator"

@Options({
  components: {
    Placeholder,
    SideNews,
    Social,
    Footer,
  },
  methods: {
    reply(to: string) {
      this.comment = `@${to} ${this.comment}`
      this.$refs.add.scrollIntoView()
    },
    handleSubmit(e: Event) {
      e.preventDefault()
      if (!this.name || !this.email || !this.comment) {
        return
      }
      this.current.comments.push({
        user: this.name,
        text: this.comment,
        time: new Date().toLocaleString(),
      })
      this.comment = ""
    },
  },
  mounted() {
    this.id = parseInt(this.$route.params.id)
    const current = data.filter((item) => item.id === this.id)
    if (!current.length) {
      this.$router.push("/")
      return
    }
    this.current = current[0]
    this.$watch(
      () => this.$route.params.id,
      (_id: string) => {
        if (this.$route.name !== "news") return
        const id = parseInt(_id)
        this.id = id
        const current = data.filter((item) => item.id === id)
        if (!current.length) {
          this.$router.push("/")
          return
        }
        this.current = current[0]
      }
    )
  },
})
export default class NewsView extends Vue {
  news = data as News[]
  id = 0
  current!: News
  name = ""
  email = ""
  comment = ""
  @Ref("add") add!: HTMLHeadingElement
}
